<script>
  /**
   * 🗺️ MapTab.vue - 火災地圖組件 (Fire Map Component)
   *
   * 這是一個火災地圖組件，專為顯示火災地點數據設計。
   * 主要功能：
   * - 顯示火災地點的 GeoJSON 數據
   * - 支援自由縮放和拖拽
   * - 支援多種底圖切換
   * - 響應式設計
   *
   * 技術架構：
   * - Vue 3 Composition API
   * - Leaflet 地圖庫
   * - Pinia 狀態管理
   * - Bootstrap 5 樣式
   */

  import { ref, onMounted, onUnmounted, watch, nextTick } from 'vue';
  import L from 'leaflet';
  import 'leaflet/dist/leaflet.css';
  import 'leaflet.heat';
  import { useDataStore } from '@/stores/dataStore.js';
  import { useDefineStore } from '@/stores/defineStore.js';

  export default {
    name: 'MapTab',
    emits: ['map-ready'],
    setup(props, { emit }) {
      // 📦 存儲實例
      const dataStore = useDataStore();
      const defineStore = useDefineStore();

      // 🗺️ 地圖相關變數
      const mapContainer = ref(null);
      let mapInstance = null;
      let currentTileLayer = null;
      let savedMarkersLayer = null;
      let worldMapLayer = null;
      let heatmapInstance = null;

      // 🎛️ 顯示模式控制
      const displayMode = ref('heatmap'); // 'point' 或 'heatmap'

      // 🔥 熱力圖配置 - 精緻火災風格
      const heatmapConfig = ref({
        radius: 12, // 熱力圖半徑（像素）- 更精緻的火焰擴散
        maxZoom: 18,
        max: 1.0,
        minOpacity: 0.9, // 極高透明度讓火焰更精緻
        blur: 25, // 更糊的火焰邊緣模糊
        gradient: {
          0.0: 'black', // 黑色 - 無火災
          0.1: 'darkred', // 深紅色 - 低強度火災
          0.2: 'maroon', // 栗色 - 小火災
          0.3: 'red', // 紅色 - 中等火災
          0.4: 'orangered', // 橙紅色 - 中高火災
          0.5: 'orange', // 橙色 - 高火災
          0.6: 'gold', // 金色 - 高溫火災
          0.7: 'yellow', // 黃色 - 極高溫火災
          0.8: 'lightyellow', // 淺黃色 - 白熱火災
          0.9: 'lightyellow', // 淺黃色 - 最高溫火災
          0.95: 'lightyellow', // 淺黃色 - 極高溫火災
          1.0: 'white', // 白色 - 極限火災
        },
      });

      // 🎛️ 地圖控制狀態
      const isMapReady = ref(false);
      const mapContainerId = ref(`leaflet-map-${Math.random().toString(36).substr(2, 9)}`);

      /**
       * 🏗️ 創建地圖實例
       * 初始化 Leaflet 地圖並設定基本配置
       */
      const createMap = () => {
        if (!mapContainer.value) return false;

        const rect = mapContainer.value.getBoundingClientRect();
        if (rect.width === 0 || rect.height === 0) {
          console.warn('[MapTab] 容器尺寸為零，延遲初始化');
          return false;
        }

        try {
          mapInstance = L.map(mapContainer.value, {
            center: defineStore.mapView.center,
            zoom: defineStore.mapView.zoom,
            zoomControl: false, // 禁用縮放控制
            attributionControl: false, // 禁用屬性控制
            dragging: true, // 啟用拖拽
            touchZoom: true, // 啟用觸控縮放
            doubleClickZoom: true, // 啟用雙擊縮放
            scrollWheelZoom: true, // 啟用滾輪縮放
            boxZoom: true, // 啟用框選縮放
            keyboard: true, // 啟用鍵盤控制
          });

          // 綁定地圖事件
          mapInstance.on('zoomend', handleZoomEnd);
          mapInstance.on('moveend', handleMoveEnd);

          // 創建自定義 pane 來控制圖層順序
          // 世界地圖 pane (最底層)
          const worldPane = mapInstance.createPane('worldPane');
          worldPane.style.zIndex = 200; // 比預設的 overlayPane (400) 低

          // 熱力圖 pane (中間層)
          const heatmapPane = mapInstance.createPane('heatmapPane');
          heatmapPane.style.zIndex = 400; // 和 overlayPane 同層級

          // 標記 pane (最上層)
          const markerPane = mapInstance.createPane('markerPane');
          markerPane.style.zIndex = 600; // 比 overlayPane 高

          // 設定 popup 面板的 z-index
          mapInstance.getPane('popupPane').style.zIndex = 2200;

          isMapReady.value = true;
          emit('map-ready', mapInstance);

          console.log('[MapTab] 地圖創建成功');
          return true;
        } catch (error) {
          console.error('[MapTab] 地圖創建失敗:', error);
          return false;
        }
      };

      /**
       * 📡 處理縮放結束事件
       * 更新地圖視圖狀態到存儲中
       */
      const handleZoomEnd = () => {
        if (mapInstance) {
          const zoom = mapInstance.getZoom();
          const center = mapInstance.getCenter();
          defineStore.setMapView([center.lat, center.lng], zoom);
          emit('update:zoomLevel', zoom);
        }
      };

      /**
       * 📡 處理移動結束事件
       * 更新地圖中心座標
       */
      const handleMoveEnd = () => {
        if (mapInstance) {
          const center = mapInstance.getCenter();
          defineStore.setMapView([center.lat, center.lng], mapInstance.getZoom());
          emit('update:currentCoords', { lat: center.lat, lng: center.lng });
        }
      };

      /**
       * 🎨 設定底圖 - 黑色海洋風格
       * 設定黑色海洋背景和世界地圖邊界
       */
      const setBasemap = () => {
        if (!mapInstance) return;

        // 移除現有底圖
        if (currentTileLayer) {
          mapInstance.removeLayer(currentTileLayer);
          currentTileLayer = null;
        }

        // 設定地圖容器背景為 my-color-black（海洋區域）
        const mapContainer = document.getElementById(mapContainerId.value);
        if (mapContainer) {
          mapContainer.style.backgroundColor = 'var(--my-color-black)';
        }

        console.log('🗺️ 設定黑色海洋背景和世界地圖邊界');
      };

      /**
       * 🌍 載入世界地圖
       */
      const loadWorldMap = async () => {
        try {
          console.log('🌍 開始載入世界地圖...');
          const response = await fetch(
            '/30DayMapChallenge-15_Fire/data/ne_110m_admin_0_countries.geojson'
          );
          if (!response.ok) {
            console.warn('⚠️ 世界地圖文件不存在，跳過載入');
            return;
          }
          const worldData = await response.json();

          // 移除現有的世界地圖
          if (worldMapLayer) {
            mapInstance.removeLayer(worldMapLayer);
          }

          // 創建世界地圖圖層作為主要背景，使用自定義 pane
          worldMapLayer = L.geoJSON(worldData, {
            pane: 'worldPane', // 使用最底層的 pane
            style: {
              fillColor: 'var(--my-color-gray-900)', // my-color-gray-900 填充
              weight: 1,
              opacity: 0.3, // 更淡的邊界透明度
              color: 'var(--my-color-gray-600)', // my-color-gray-600 邊界
              fillOpacity: 0.9, // 高透明度
            },
          });

          // 添加到地圖
          worldMapLayer.addTo(mapInstance);

          console.log('🌍 世界地圖載入完成');
        } catch (error) {
          console.warn('⚠️ 載入世界地圖失敗，繼續載入儲存的地點:', error);
        }
      };

      /**
       * 📍 載入並顯示儲存的地點
       */
      const loadSavedLocations = async () => {
        try {
          console.log('📍 開始載入儲存的地點...');
          console.log('📍 當前 dataStore:', dataStore);
          await dataStore.loadSavedLocations();
          console.log('📍 儲存的地點載入完成，數量:', dataStore.savedLocations.length);
          console.log('📍 前3個地點:', dataStore.savedLocations.slice(0, 3));
          displaySavedLocations();
        } catch (error) {
          console.error('❌ 載入儲存的地點失敗:', error);
        }
      };

      /**
       * 🔥 在地圖上顯示儲存的地點
       */
      const displaySavedLocations = () => {
        console.log(
          '🔥 嘗試顯示儲存的地點，模式:',
          displayMode.value,
          '地圖實例:',
          !!mapInstance,
          '地點數量:',
          dataStore.savedLocations.length
        );
        if (!mapInstance || !dataStore.savedLocations.length) {
          console.log('❌ 無法顯示儲存的地點：地圖未準備好或沒有數據');
          return;
        }

        // 清除現有圖層
        if (savedMarkersLayer) {
          mapInstance.removeLayer(savedMarkersLayer);
        }
        if (heatmapInstance) {
          mapInstance.removeLayer(heatmapInstance);
        }

        if (displayMode.value === 'point') {
          displayPoints();
        } else {
          displayHeatmap();
        }
      };

      /**
       * 📍 顯示點位
       */
      const displayPoints = () => {
        savedMarkersLayer = L.layerGroup();

        dataStore.savedLocations.forEach((location) => {
          const [lng, lat] = location.geometry.coordinates;
          const properties = location.properties;

          // 創建地點標記 - 小紅圓
          const locationIcon = L.divIcon({
            className: 'location-marker',
            html: '<div class="location-marker-icon"></div>',
            iconSize: [12, 12],
            iconAnchor: [6, 6],
          });

          const marker = L.marker([lat, lng], { icon: locationIcon });

          // 創建彈出窗口內容
          const popupContent = `
            <div class="location-popup">
              <div class="popup-header">
                <h6 class="popup-title">${properties.location?.name || '未知地點'}</h6>
              </div>
              <div class="popup-content">
                <div class="popup-item">
                  <span class="popup-label">地址</span>
                  <span class="popup-value">${properties.location?.address || '無地址資訊'}</span>
                </div>
                <div class="popup-item">
                  <span class="popup-label">國家</span>
                  <span class="popup-value">${properties.location?.country_code || 'Unknown'}</span>
                </div>
                <div class="popup-item">
                  <span class="popup-label">日期</span>
                  <span class="popup-value">${new Date(properties.date).toLocaleDateString()}</span>
                </div>
                ${
                  properties.google_maps_url
                    ? `
                  <div class="popup-actions">
                    <a href="${properties.google_maps_url}" target="_blank" class="popup-btn">
                      查看 Google 地圖
                    </a>
                  </div>
                `
                    : ''
                }
              </div>
            </div>
          `;

          marker.bindPopup(popupContent);
          savedMarkersLayer.addLayer(marker);
        });

        mapInstance.addLayer(savedMarkersLayer);

        console.log(`📍 已在地圖上顯示 ${dataStore.savedLocations.length} 個點位`);
      };

      /**
       * 🔥 顯示熱力圖
       */
      const displayHeatmap = () => {
        // 移除現有熱力圖
        if (heatmapInstance) {
          mapInstance.removeLayer(heatmapInstance);
        }

        // 準備熱力圖數據
        const heatData = dataStore.savedLocations.map((location, index) => {
          const [lng, lat] = location.geometry.coordinates;
          // 計算強度值（0-1之間）
          const intensity = Math.min(1, (index + 1) / dataStore.savedLocations.length);
          return [lat, lng, intensity];
        });

        // 創建熱力圖圖層，並指定使用 heatmapPane
        heatmapInstance = L.heatLayer(heatData, {
          ...heatmapConfig.value,
          pane: 'heatmapPane', // 使用中間層的 pane
        });

        // 添加到地圖
        mapInstance.addLayer(heatmapInstance);

        console.log(`🔥 已在地圖上顯示 ${dataStore.savedLocations.length} 個地點的熱力圖`);
      };

      /**
       * 🔄 切換顯示模式
       */
      const toggleDisplayMode = (mode) => {
        displayMode.value = mode;
        displaySavedLocations();
      };

      /**
       * 📏 刷新地圖尺寸
       * 當容器大小改變時重新計算地圖尺寸
       */
      const invalidateSize = () => {
        if (mapInstance) {
          setTimeout(() => {
            mapInstance.invalidateSize();
          }, 100);
        }
      };

      /**
       * 🚀 初始化地圖
       * 創建地圖並載入初始數據
       */
      const initMap = () => {
        let attempts = 0;
        const maxAttempts = 20;

        const tryCreateMap = () => {
          if (attempts >= maxAttempts) {
            console.error('[MapTab] 地圖初始化失敗，已達到最大嘗試次數');
            return;
          }

          attempts++;
          console.log(`[MapTab] 嘗試創建地圖 (${attempts}/${maxAttempts})`);

          if (createMap()) {
            console.log('[MapTab] 地圖創建成功，開始初始化');
            setBasemap();
            // 延遲載入世界地圖和儲存的地點，確保地圖完全準備好
            setTimeout(async () => {
              await loadWorldMap();
              await loadSavedLocations();
            }, 500);
          } else {
            console.log('[MapTab] 地圖創建失敗，100ms 後重試');
            setTimeout(tryCreateMap, 100);
          }
        };

        tryCreateMap();
      };

      // 📏 設置 ResizeObserver 監聽容器大小變化
      let resizeObserver = null;
      let resizeTimeout = null;

      const setupResizeObserver = () => {
        if (!mapContainer.value || !window.ResizeObserver) return;

        resizeObserver = new ResizeObserver(() => {
          if (resizeTimeout) {
            clearTimeout(resizeTimeout);
          }

          resizeTimeout = setTimeout(() => {
            console.log('🔄 容器大小變化，刷新地圖');
            invalidateSize();
          }, 200);
        });

        resizeObserver.observe(mapContainer.value);
        console.log('✅ ResizeObserver 已設置');
      };

      // 🧹 生命週期：組件掛載
      onMounted(() => {
        nextTick(() => {
          initMap();
          setupResizeObserver();
        });
      });

      // 🧹 生命週期：組件卸載
      onUnmounted(() => {
        if (resizeTimeout) {
          clearTimeout(resizeTimeout);
        }

        if (resizeObserver) {
          resizeObserver.disconnect();
        }

        if (mapInstance) {
          mapInstance.remove();
          mapInstance = null;
        }

        currentTileLayer = null;
        savedMarkersLayer = null;
        worldMapLayer = null;
        heatmapInstance = null;
        isMapReady.value = false;
      });

      // 👀 監聽器：監聽儲存的地點數據變化
      watch(() => dataStore.savedLocations, displaySavedLocations, { deep: true });

      // 👀 監聽器：監聽底圖變化
      watch(
        () => defineStore.selectedBasemap,
        () => {
          if (isMapReady.value) {
            setBasemap();
          }
        }
      );

      // 📤 返回組件公開的屬性和方法
      return {
        mapContainer,
        mapContainerId,
        invalidateSize,
        defineStore,
        dataStore,
        displayMode,
        toggleDisplayMode,
      };
    },
  };
</script>

<template>
  <!-- 🗺️ 地圖主容器 -->
  <div id="map-container" class="h-100 w-100 position-relative bg-transparent z-0">
    <!-- 🎛️ 左側中間控制面板 -->
    <div
      class="position-absolute"
      style="top: 50%; left: 0; transform: translateY(-50%); z-index: 1000; padding: 1rem"
    >
      <div class="bg-dark bg-opacity-75 rounded-3 p-3">
        <!-- 🎛️ 顯示模式選擇區域 -->
        <div class="">
          <div class="d-flex flex-column gap-1">
            <button
              type="button"
              class="btn border-0 my-country-btn my-font-sm-white px-4 py-3"
              :class="[displayMode === 'point' ? 'active' : '']"
              @click="toggleDisplayMode('point')"
            >
              POINT
            </button>
            <button
              type="button"
              class="btn border-0 my-country-btn my-font-sm-white px-4 py-3"
              :class="[displayMode === 'heatmap' ? 'active' : '']"
              @click="toggleDisplayMode('heatmap')"
            >
              HEATMAP
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- 🗺️ Leaflet 地圖容器 -->
    <div :id="mapContainerId" ref="mapContainer" class="h-100 w-100"></div>
  </div>
</template>

